*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: spaceGrotesk;
}

.App{
  height: 100vh;
  width: 100vw;
  display: flex;
}

.first-half{
  background-image: url(/public/bg-main-desktop.png);
  background-size: cover;
  height: 100vh;
  width: 30%;
}

.second-half{
  height: 100vh;
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
}


form{
  display: inline-grid;
  width: auto;
  height: auto;
  align-items: center;
}

label{
  font-size: 10px;
  font-weight: bold;
  margin-top: 8px;
  margin-bottom: 4px;
}

.inputs{
  height: 38px;
  width: 280px;
  font-size: medium;
  letter-spacing: 1px;
  border-radius: 6px;
  padding-left: 8px;
  margin: 0 0 2px 0;
  border: 1px rgba(170, 163, 163, 0.651) solid ;
}

.err {
  border: 1px red solid;
  outline: none;
  
}

.dateContainer{
  display: flex;
  flex-direction: column;
  width: 138px;
  height: auto;
}

.dateCvcLabel{
  margin: 0 0 4px;
  height: 11px;
}

.dateInnerContainer{
  display: flex;
  justify-content: space-between;
}

.dateinputs{
  width: 65px;
  height: 38px;
  border-radius: 6px;
  border: 1px rgba(170, 163, 163, 0.651) solid ;
  margin: 0 0 2px 0;
  text-align: center;
}

.dateerr{
  border: red 1px solid;
  outline: none;
}

.dateNdCvcContainer{
  display: flex;
  height: auto;
  margin: 8px 0 0 0;
}

.cvcContainer{
  height: auto;
  margin-left: 10px;
}

.cvcInnerContainer{
  display: flex;
  flex-direction: column;
}

.cvcinput{
  width: 132px;
  height: 38px;
  border-radius: 6px;
  border: 1px rgba(170, 163, 163, 0.651) solid ;
  margin: 0 0 2px 0;
  text-align: center;
}

.cvcerr{
  border: 1px red solid;
  outline: none;
}

.error{
  color: red;
  font-size: small;
}

::placeholder{
  padding: 4px 0 4px 10px;
  font-size: 14px;
  color: rgba(170, 163, 163, 0.651);
}

.btn{
  color: white;
  background-color: hsl(278, 68%, 11%);
  height: 38px;
  width: 280px;
  border-radius: 6px;
  border: none;
  margin-top: 16px;
}

.thanks{
  text-align: center;
  margin: 0 0 20px;
}

.checkMark{
  width: 61px;
  height: 61px;
  margin: 0 auto 20px auto ;

}

.thankyou{
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.card-front{
  background-image: url(/public/bg-card-front.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 8px;
  z-index: 3;
  height: 200px;
  width: 360px;
  position: fixed;
  margin: 120px 0 0 180px;
  box-shadow: 10px 10px 30px 2px rgba(130, 130, 130, 0.247);
  padding: 20px;
}

.card-back{
  background-image: url(/public/bg-card-back.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position:center;
  border-radius: 8px;
  z-index: 4;
  height: 200px;
  width: 360px;
  position: fixed;
  margin: 340px 0 0 260px;
  box-shadow: 10px 10px 30px 2px rgba(130, 130, 130, 0.247);
}

.circle-container{
  display: flex;
  align-items: center;
}

.circle-one{
  height: 40px;
  width: 40px;
  background-color: white;
  border-radius: 50%;
}

.circle-two{
  height: 20px;
  width: 20px;
  border: 0.5px white solid;
  border-radius: 50%;
  margin-left: 14px;
}

.card-num{
  font-weight: bold;
  font-size: 18px;
  letter-spacing: 4px;
  color: white;
  margin: 54px 0 20px 0;
}

.name-expd{
  display: flex;
  color: white;
  justify-content: space-between;
  letter-spacing: 1px;
}

.cvv-number{
  display: flex;
  justify-content: flex-end;
  color: white;
  font-size: 14px;
  letter-spacing: 2px;
  margin: 92px 40px 0 0;
}

@font-face{
  font-family: spaceGrotesk;
  src: url(https://fonts.google.com/specimen/Space+Grotesk);
}
